<template>
    <div :style="{'padding-left':'20px!important'}" class="item-holder">
        <div v-for="(l, k, i) in item" :key="k">
            <b-row>
                <strong
                    v-if="typeof l !== 'number' && typeof l !== 'string' && typeof l !== 'boolean'"
                >
                    <a
                        href="/"
                        v-if="expanded[typeof k === 'number' ? k : i]"
                        @click.prevent="() => {collapse(typeof k === 'number' ? k : i)}"
                    >
                        -
                        <span>{{i18n.get(k)}}</span>
                    </a>
                    <a
                        href="/"
                        v-else
                        @click.prevent="() => {expand(typeof k === 'number' ? k : i)}"
                    >
                        +
                        <span>{{i18n.get(k)}}</span>
                    </a>
                </strong>
                <div class="list-actions" v-if="k !== 'coords' ">
                    <div v-if="Array.isArray(l)">
                        <b-button
                            block
                            variant="primary btn-circle"
                            @click="addElementToList(item, l, k, i)"
                        >+</b-button>
                    </div>
                    <div v-if="typeof k === 'number' && k !== 0">
                        <b-button
                            block
                            variant="danger btn-circle"
                            @click="removeElementFromList(item, l, k, i)"
                        >-</b-button>
                    </div>
                </div>
            </b-row>

            <b-form-group
                v-if="typeof l === 'number' || typeof l === 'string' || typeof l === 'boolean'"
                :label="i18n.get(k)"
                :label-for="(path + '.' + k).replace(/\./g, '_')"
                :label-cols="3"
                :horizontal="false"
            >
                <!-- <b-form-textarea
                    rows="4"
                    v-if="k==='desc'"
                    :id="(path + '.' + k).replace(/\./g, '_')"
                    type="text"
                    :value="l.toString()"
                ></b-form-textarea>-->
                <b-form-checkbox-group
                    v-if="typeof l === 'boolean'"
                    stacked
                    name="Checkboxes"
                    :plain="true"
                    :checked="l === true ? [0]: []"
                >
                    <b-form-checkbox
                        value="0"
                        :id="(path + '.' + k).replace(/\./g, '_')"
                    >{{i18n.get(k)}}</b-form-checkbox>
                </b-form-checkbox-group>
                <div
                    v-else-if="((path + '.' + k).indexOf('icon') !== -1  && (path + '.' + k).indexOf('href') === -1) ||  (path + '.' + k).indexOf('src') !== -1 || (path + '.' + k).indexOf('avatar') !== -1 || (path + '.' + k).indexOf('thumbnail') !== -1"
                    style="display:grid; grid-gap:10px;justify-items:flex-start"
                >
                    <b-form-textarea
                        :id="(path + '.' + k).replace(/\./g, '_')"
                        type="text"
                        :rows="6"
                        :max-rows="6"
                        autocomplete="name"
                        :value="l.toString()"
                        :disabled="k==='id'"
                        v-if="k === 'desc'"
                    ></b-form-textarea>

                    <b-form-input
                        v-else
                        :id="(path + '.' + k).replace(/\./g, '_')"
                        type="text"
                        autocomplete="name"
                        :value="l.toString()"
                        :disabled="k==='id'"
                    ></b-form-input>

                    <b-form-file
                        :plain="true"
                        :id="('file-input' + path + '.' + k).replace(/\./g, '_')"
                    ></b-form-file>
                    <b-progress
                        style="justify-self:stretch"
                        v-if="uploadProgress !== 0"
                        :value="uploadProgress"
                        :max="100"
                        show-progress
                        animated
                    ></b-progress>
                    <b-button
                        type="upload"
                        size="sm"
                        variant="primary"
                        @click.prevent="(e)=>{uploadFile(e, (path + '.' + k).replace(/\./g, '_'))}"
                    >
                        <i class="fa fa-upload"></i> Загрузить
                    </b-button>
                </div>
                <b-form-textarea
                    :id="(path + '.' + k).replace(/\./g, '_')"
                    type="text"
                    :rows="6"
                    :max-rows="6"
                    autocomplete="name"
                    :value="l.toString()"
                    :disabled="k==='id'"
                    v-else-if="k === 'desc'"
                ></b-form-textarea>
                <b-form-input
                    v-else
                    :id="(path + '.' + k).replace(/\./g, '_')"
                    type="text"
                    autocomplete="name"
                    :disabled="k==='id'"
                    :value="l.toString()"
                ></b-form-input>
            </b-form-group>

            <FormTreeElement
                v-show="expanded[typeof k === 'number' ? k : i]"
                v-else
                :item="l"
                :path="path + '.' + k"
                :level="level+1"
            ></FormTreeElement>
        </div>
    </div>
</template>
<script>
import i18n from "../../i18n.js";
import * as firebaseui from "firebaseui"
import firebase from "../../firebase.js";
import { setTimeout } from "timers";
export default {
    props: {
        level: Number,
        path: String,
        item: {
            default: {}
        }
    },
    name: "FormTreeElement",
    data() {
        return {
            uploadProgress: 0,
            i18n: i18n,
            selected: [], // Must be an array reference!
            show: true,
            data: undefined,
            expanded: undefined
        };
    },
    created() {
        this.expanded = [];
        if (this.item.hasOwnProperty("length")) {
            for (let i = 0; i < this.item.length; i++) {
                this.expanded.push(false);
            }
        } else {
            for (let key in this.item) {
                this.expanded.push(false);
            }
        }
    },
    methods: {
        expand(index) {
            this.expanded[index] = true;
            console.log(this.expanded, index);
            this.expanded = [...this.expanded];
        },
        collapse(index) {
            this.expanded[index] = false;
            this.expanded = [...this.expanded];
        },
        addElementToList(item, l, k, i) {
            console.log(item, l, k, i);
            if (l.length > 0) {
                if (typeof l[0] === "string") {
                    l.push(l[0]);
                } else {
                    var copy = JSON.parse(JSON.stringify(l[0])); //Object.assign({}, l[0]);
                    l.push(copy);
                }
            }
        },
        removeElementFromList(item, l, k, i) {
            console.log(item, l, k, i);
            item.splice(k, 1);
        },
        addElement() {},
        click() {
            // do nothing
        },
        uploadFile(e, id) {
            var input = document.getElementById("file-input" + id);
            const file = input.files[0];
            if (file) {
                console.log(file);

                const name = +new Date() + "-" + file.name;
                const metadata = { contentType: file.type };
                const ref = firebase.storage().ref();
                const task = ref.child("uploads/" + name).put(file, metadata);
                task.then(snapshot => snapshot.ref.getDownloadURL())
                    .then(url => {
                        console.log(url);
                        var inputField = document.getElementById(id);
                        inputField.value = url;
                        setTimeout(() => {
                            this.uploadProgress = 0;
                        }, 1000);
                    })
                    .catch(err => {
                        console.log(err);
                    });
                task.on("state_changed", snapshot => {
                    this.uploadProgress =
                        (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                    console.log(this.uploadProgress);
                });
            }
        }
    }
};
</script>

