<template>
    <b-col md="12" :key="k">
        <template v-if="sorting && data">
            <b-card 
                v-for="(key, index) in sorting" 
                :key="index" 
                :class="[{'collapsed':!expanded[index]}]"
            >
                <div slot="header">
                    <strong><a href="/" v-if="expanded[index]" @click.prevent="() => {collapse(index)}">- <span>{{i18n.get(key)}}</span></a>
                    <a href="/" v-else @click.prevent="() => {expand(index)}">+ <span>{{i18n.get(key)}}</span></a></strong>
                    <b-button
                        style="margin-left:20px"
                        type="submit"
                        size="sm"
                        variant="primary btn-circle"
                        v-if="addBlockButton"
                        @click="()=>{addClickHandler()}"
                    >+</b-button>
                </div>
                <b-form @submit.prevent="submitHandler" :id="'form_' + key" v-show="expanded[index]">
                    <FormTreeElement :item="data[key]" :path="key" :level="0"></FormTreeElement>
                    <div slot="footer" class="block-footer">
                        <b-button type="submit" size="sm" variant="primary">
                            <i class="fa fa-dot-circle-o"></i> Сохранить
                        </b-button>
                    </div>
                </b-form>
            </b-card>
        </template>
        <template v-else-if="data">
            <b-card 
                v-for="(item, key, index) in data" 
                :key="index" 
                :class="[{'collapsed':!expanded[index]}]"
            >
                <div slot="header">
                    <strong><a href="/" v-if="expanded[index]" @click.prevent="() => {collapse(index)}">- <span>{{i18n.get(key)}}</span></a>
                    <a href="/" v-else @click.prevent="() => {expand(index)}">+ <span>{{i18n.get(key)}}</span></a></strong>
                    <b-button
                        style="margin-left:20px"
                        type="submit"
                        size="sm"
                        variant="primary btn-circle"
                        v-if="addBlockButton"
                        @click="()=>{addClickHandler()}"
                    >+</b-button>
                </div>
                <b-form @submit.prevent="submitHandler" :id="'form_' + key" v-show="expanded[index]">
                    <slot v-if="useCustomTemplate" :item="item">
                        
                    </slot>
                    <FormTreeElement v-else :item="item" :path="key" :level="0"></FormTreeElement>
                    <div slot="footer" class="block-footer">
                        <b-button type="submit" size="sm" variant="primary">
                            <i class="fa fa-dot-circle-o"></i> Сохранить
                        </b-button>
                    </div>
                </b-form>
            </b-card>
        </template>
    </b-col>
</template>

<script>
import FormTreeElement from "./FormTreeElement.vue";
import AppData from "../../AppData.js";
import i18n from "../../i18n.js";
import firebase from "../../firebase.js";
import { setTimeout } from "timers";
export default {
    props: {
        useCustomTemplate:Boolean,
        blockTitle: {
            default: ""
        },
        addBlockButton: Boolean,
        collection: String,
        sorting: Array,
    },
    data() {
        return {
            k: 0,
            firebase: firebase,
            modal: false,
            i18n: i18n,
            data: undefined,
            expanded:undefined,
        };
    },
    created() {
        this.expanded = []
        if (this.data != undefined) {

            if (this.data.hasOwnProperty('length')) {
                for(let i = 0; i < this.data.length ;i++) {
                    this.expanded.push(false);
                }
            } else {
                for(let key in this.data) {
                    this.expanded.push(false);
                }
            }
        }
    },
    mounted() {
        firebase.auth().onAuthStateChanged(user => {
            if (user) {
                AppData.fetchData(this.collection)
                    .then(result => {
                        this.data = result;
                        console.log(result);
                    })
                    .catch(err => {
                        console.log(err);
                    });
            } else {
                this.$router.push("/pages/login/");
                // Implement logic to trigger the login dialog here or redirect to sign-in page.
                // e.g. showDialog()
            }
        });
    },
    methods: {
        expand(index) {
            this.expanded[index] = true;
            console.log(this.expanded, index)
            this.expanded = [...this.expanded ]
        },
        collapse(index) {
            this.expanded[index]  = false;
            this.expanded = [...this.expanded ]
        },
        addClickHandler() {
            var i = 1;
            var template;
            while (this.data[this.blockTitle + i] !== undefined) {
                template = this.data[this.blockTitle + i];
                i++;
            }
            this.data[this.blockTitle + i] = Object.assign({}, template);
            this.data[this.blockTitle + i].id = i;

            this.data = this.data;
            AppData.saveData(
                this.collection,
                this.blockTitle + i,
                this.data[this.blockTitle + i]
            );
            this.k++;
        },
        showAuth() {
            this.modal = true;
        },
        submitHandler(e) {
            Array.from(e.target.elements).forEach(element => {
                var data = this.data;
                if (element.id) {
                    console.log(element.id);
                    let objectPath = element.id
                        .replace(/[_]/g, ".")
                        .replace(/\.(\d{1,2})/g, "[$1]");
                    console.log(objectPath);
                    if (element.id.indexOf("file-input") !== -1) {
                        console.log(element.checked);
                    } else if (element.type === "checkbox") {
                        console.log(element.checked);
                        eval("data." + objectPath + "= element.checked");
                    } else {
                        console.log(element.value);
                        eval("data." + objectPath + "= element.value");
                    }
                }
            });
            var doc = e.target.id.replace("form_", "");
            AppData.saveData(this.collection, doc, this.data[doc]);
        }
    },
    components: {
        FormTreeElement
    }
};
</script>

<style lang="scss">
.collapsed {
    .card-body {
        display:none;
    }
}
a {
    color:#000;
    &:hover {
     text-decoration: none;
    }
}
.block-footer {
    margin-top:20px;
}
</style>